<template lang="pug">
  div.search-user 
    section.ui-input-wrap.ui-border-t( @keydown.enter="search" )
      .ui-input.ui-border-radius
        input(type='text', name='', value='', placeholder='我也说一句...', v-model="keyword")
      button.ui-btn 取消
    .search-rs( v-show="userList.length" )
      ul.user-item( v-for="user in userList")
        li.user-list( @click="toUserCard(user.account)" )
          span {{user.nickname}}
          span {{user.account}}
   
</template>
<script>
import { socketEmit } from '@/socket/socket'
export default {
  name: 'searchUser',
  data() {
    return {
      keyword: '',
      userList: []
    }
  },
  methods: {
    toUserCard(account) {
      this.$router.push({
        name: 'usercard',
        params: {
          account 
        }
      })
    },
    search() {
      if(this.keyword === '') { this.userList = []; return; };
      socketEmit('searchUser', { keyword: this.keyword }).then(res => {
        console.log(res, 'res');
        let data = res.data;
        this.userList = data;
      })
    }
  }
}
</script>
<style lang="stylus" scoped>
  .search-user 
    .user-list 
      border-bottom 1px solid #cccccc
</style>





